.App {
  display: flex;

  text-align: center;
  flex-direction: column;
  width: 100%;
}
.start{

}
.zag{
  word-wrap: break-word;
  font-size: 32px;
}
.start span{
  word-wrap: break-word;
  font-size: 26px;
  color: #4653b1;
  text-decoration: underline;
  cursor: pointer;
  text-align: center;
  transition: all 0.3s ease;
}
.start span:hover {
  color: #abb2f3;
}
.button_m{
  font-size: 26px;
  text-transform: uppercase;
  cursor: pointer;
  padding: 5px 10px;
  border:  1px solid #61dafb;
  border-radius: 10px;
  transition: all 0.3s ease;
}
.button_m:hover{
  background-color: #61dafb;
  color: #282c34;
}

.zagadka{
  font-size: 26px;
  max-width: 1200px;
  margin: 0 auto;
}
.input_text{
  font-size: 32px;
  border:  1px solid #61dafb;
  border-radius: 10px;
  transition: all 0.3s ease;
  background-color: transparent;
  outline: none;
  margin-top: 15px;
  padding: 5px;
  color: #61dafb;


}
.zagadka::selection{
  background-color: transparent;

}
@media screen and (max-width: 450px) {
  .input_text{
    width: 250px;
  }
}